import { useEffect, useState } from 'react';
import '../Assets/styles/auth.css';
import { AuthConnexion } from '../RoutesSubComponents/ConnexionComponents';
import { AuthInscription } from '../RoutesSubComponents/InscriptionComponents';
import { useParams } from 'react-router-dom';
import { Fade } from 'react-reveal';


const Auth = () => {
    const urlParams = useParams();
    const tab = [
        { title: 'Connectez vous', component: <AuthConnexion /> },
        { title: 'Inscrivez vous', component: <AuthInscription /> },
    ]
    const getAuthView = () => {
        if (urlParams.typeAuth === 'Connexion') {
            setAuthView(tab[0])
        } else if (['Inscription'].includes(urlParams.typeAuth)) {
            setAuthView(tab[1])
        }
        else {
            console.log(urlParams)
        }
    }
    const [authView, setAuthView] = useState({});

    useEffect(() => getAuthView(), [urlParams])

    return <div className="authPart" >

        <img alt={'Authentication wave Banner'}
            src={require('../Assets/images/illustrations/wave-1.svg').default} />
        <div className='authPart-form'>
            <Fade left>
                <div className="apf-img">
                    <img alt={'Authentication simple Banner'}
                        src={require('../Assets/images/illustrations/register2.svg').default} />
                </div>
            </Fade>

            <Fade right>
                <div className="apf-form">
                    <h2>{authView.title}</h2>
                    {
                        authView.component
                    }
                </div>
            </Fade>
        </div>
    </div>
}

export default Auth;