import { Fade, Reveal } from 'react-reveal'
import { Link } from 'react-router-dom'
// import slider from 'react-slick/lib/slider';
import Slider from 'react-slick'

export const AccueilBanner = () => {
    return <div className="accueilBanner">
        <section className="accueilBanner-text">
            <h1>En quelque clic,<br /> trouvez le travail ou le job de vos rêve</h1>
            <form className="accueilBanner-form">
                <input placeholder="Secteur d'activités ..." />
                <input placeholder="Region..." />
                <div>
                    <button className='semiRounded'>Chercher</button>
                    <Link className='semiRounded' to='/Authentification/Inscription'>S'inscrire</Link>
                </div>
            </form>

        </section>
        <Fade right>
            <section className='accueilBanner-img'>

            </section>
        </Fade>
        <Reveal top>
            <img alt='Home BAnner' src={require('../Assets/images/illustrations/banner1.svg').default} />
        </Reveal>
    </div>
}


export const TypeServices = () => {
    return <div className='accueilTypeServices'>
        <Reveal left>
            <section className='accueilTypeService'>
                <img alt='Employeur Account Explain Banner'
                    src={require('../Assets/images/illustrations/employeur1.svg').default} />
                <div>
                    <h2>Vous êtes un employeur ?</h2>
                    <p>Publiez une offre et trouvez le profil dont vous avez besoin</p>
                    <Link to="/Authentification/Inscription">S'inscrire</Link>
                </div>
            </section>
        </Reveal>

        <Reveal right>
            <section className='accueilTypeService'>
                <img alt='Candidate Account Explain Banner'
                    src={require('../Assets/images/illustrations/worker1.svg').default} />
                <div>
                    <h2>Vous êtes a la recherche d'emplois ?</h2>
                    <p>Déposez votre CV et décrochez votre prochain emploi</p>
                    <Link to='/Authentification/Inscription'>S'inscrire</Link>
                </div>
            </section>
        </Reveal>
    </div>
}

export const Partenaires = () => {
    const settings = {
        dots: false,
        autoplay: true,
        autoplaySpeed: 2000,
        infinite: true,
        speed: 500,
        slidesToShow: 4,
        slidesToScroll: 1,
        centerMode: true,
        responsive: [
            {
                breakpoint: 512,
                settings: {
                    slidesToShow: 1,
                    slidesToScroll: 1
                }
            }
        ]
    };
    const partenaires = [{ id: 1, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 2, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 3, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 4, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 5, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 2, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 3, logo: 'partenaires/', nom: 'Partenaire ' },
    { id: 4, logo: 'partenaires/', nom: 'Partenaire ' },]
    return <div className='nosPartenaires'>
        <h2> Nos partenaires </h2>
        <section className='nosPartenaires-liste'>
            <Slider {...settings}>
                {partenaires.map(ptn => <div key={ptn.id} className='npl-item'>
                    <img alt='All Partenaire'
                        src={require('../Assets/images/' + ptn.logo + ptn.id + '.png').default} />
                    {/* <p>{ptn.nom + ptn.id} </p> */}
                    <Link to="">Consulter</Link>
                </div>
                )}
            </Slider>
        </section>
    </div>
}