import { Link } from 'react-router-dom';
import '../Assets/styles/publication.css';
const Publications = () => {
    return <div className="publicationsPart">
        <section className="publicationsPart-title">
            <h2>Retrouvez ici les dernières offres d'emplois ainsi que les offres d'emploi les plus en vue </h2>
            <img src={require('../Assets/images/illustrations/annonces1.svg').default} />
        </section>


        <section className='publicationsPart-notAccessible'>
            <p>Pour acceder au offres d'emplois veuillez vous inscrire et vous connectez </p>
            <div>
                <Link to='/Authentification/Connexion'>Connexion</Link>
                <Link to='/Authentification/Inscription'>Inscription</Link>
            </div>
        </section>
    </div>
}

export default Publications;